import './App.css';
import { HashRouter as Router, Route, NavLink, Redirect, Routes, useNavigate } from 'react-router-dom'
import { useState } from 'react';
import Find from './pages/find/index'
import Mine from './pages/mine/index'
import Friend from './pages/friend/index'
import Shop from './pages/shop/index'
import MusicPerson from './pages/musicPerson/index'
import Search from './pages/search/index'
import { useLocation } from 'react-router'
import { SearchOutlined, StepBackwardOutlined, StepForwardOutlined } from '@ant-design/icons';
import { Input } from 'antd';

import { getMusicUrl, getHotIds, getSearchMusic } from './api/index'

function App() {
  var [navItem, setNavItem] = useState(0);
  const navigate = useNavigate()
  const location = useLocation()
  console.log(location)

  var pathName = location.pathname
  switch (pathName) {
    case '/':
      // setNavItem(() => {
      //   return 0
      // })
      navItem = 0
      break;
    case '/mine':
      // setNavItem(() => {
      //   return 1
      // })
      navItem = 1

      break;
    case '/friend':
      // setNavItem(() => {
      //   return 2
      // })
      navItem = 2

      break;
    case '/shop':
      // setNavItem(() => {
      //   return 3
      // })
      navItem = 3

      break;
    case '/musicPerson':
      // setNavItem(() => {
      //   return 4
      // })
      navItem = 4

      break;
    default:
      break;
  }

  // 播放的音乐的url
  var [audioUrl, setAudioUrl] = useState('');

  // 播放的音乐列表的数组
  var [audioList, setAudioList] = useState([])
  var [audioNum, setAudioNum] = useState(0);

  // 播放的音乐的id
  const [parentCount, setParentCountt] = useState({});
  // 点击热门推荐的播放按钮
  const getChildCount = (val) => {
    console.log('设置子组件传递过来的参数', val)
    getHotIds({ id: val }).then(res => {
      setAudioList(res.data.songs)
      setAudioNum(0)
      // audioList = res.data.songs
      getMusicUrl({ id: res.data.songs[0].id }).then(res2 => {
        console.log('获取音乐的地址是', res2)
        setAudioUrl(res2.data.data[0].url)
        // 自动播放音乐
        var audioPlayer = document.getElementById('audioControls')
        console.log(audioPlayer)
        setTimeout(() => {
          audioPlayer.play()
        }, 100)
      })
    })
    setParentCountt(val);
  };
  // 点击下面音乐控件的上一首
  const prevClick = () => {
    if (audioNum == 0) {
      audioNum = audioList.length - 1
    } else {
      audioNum = audioNum - 1
    }
    console.log('上一首歌曲的num是：' + audioNum + '上一首的数据是:' + audioList[audioNum])
    getMusicUrl({ id: audioList[audioNum].id }).then(res2 => {
      console.log('获取音乐的地址是', res2)
      setAudioUrl(res2.data.data[0].url)
      setAudioNum(audioNum)
      // 自动播放音乐
      var audioPlayer = document.getElementById('audioControls')
      console.log(audioPlayer)
      setTimeout(() => {
        audioPlayer.play()
      }, 100)
    })
  }
  // 点击下面音乐控件的下一首
  const nextClick = () => {
    if (audioNum == audioList.length - 1) {
      audioNum = 0
    } else {
      audioNum = audioNum + 1
    }
    console.log('下一首歌曲的num是：' + audioNum + '下一首的数据是:' + audioList[audioNum])
    getMusicUrl({ id: audioList[audioNum].id }).then(res2 => {
      console.log('获取音乐的地址是', res2)
      setAudioUrl(res2.data.data[0].url)
      setAudioNum(audioNum)
      // 自动播放音乐
      var audioPlayer = document.getElementById('audioControls')
      console.log(audioPlayer)
      setTimeout(() => {
        audioPlayer.play()
      }, 100)
    })
  }
  // 点击上面的tab页切换
  const handleClick = (index) => {
    console.log('触发点击事件', index)
    setNavItem(() => {
      return index
    })
    switch (index) {
      case 0:
        navigate('/')
        break;
      case 1:
        navigate('/mine')
        break;
      case 2:
        navigate('/friend')
        break;
      case 3:
        navigate('/shop')
        break;
      case 4:
        navigate('/musicPerson')
        break;
      default:
        break;
    }
  };
  // 搜索
  const [searchVal, setSearchVal] = useState('');
  const handleSearch = (val) => {
    // 获取搜索的数据
    getSearchMusic({ keywords: val }).then(res => {
      console.log(res)
      // audioList = res.data.result.songs
      setAudioList(res.data.result.songs)
      // setAudioNum(0)
      setNavItem('')
      console.log(val)
      setSearchVal(val)
      navigate('/search')
    })




  }

  return (
    <div className='container'>
      <div className='nav'>
        <div className='logo'>
          音乐
        </div>
        {/* <NavLink to="/"></NavLink>
        <NavLink to="/mine"></NavLink>
        <NavLink to="/friend"></NavLink>
        <NavLink to="/shop"></NavLink>
        <NavLink to="/musicPerson"></NavLink> */}
        <div className={navItem === 0 ? "activite" : "item"} onClick={() => { handleClick(0) }}>发现</div>
        <div className={navItem === 1 ? "activite" : "item"} onClick={() => { handleClick(1) }}>我的</div>
        <div className={navItem === 2 ? "activite" : "item"} onClick={() => { handleClick(2) }}>朋友</div>
        <div className={navItem === 3 ? "activite" : "item"} onClick={() => { handleClick(3) }}>商城</div>
        <div className={navItem === 4 ? "activite" : "item"} onClick={() => { handleClick(4) }}>音乐人</div>
        <div><Input placeholder="音乐/视频电台/用户" onPressEnter={(e) => { handleSearch(e.target.value) }} prefix={<SearchOutlined />} /></div>
        <div className='loginBtn'>登录</div>
      </div>
      <div className='views'>
        <Routes>
          <Route path='/' element={<Find getCount={getChildCount} />} ></Route>
          <Route path='/mine' element={<Mine />}></Route>
          <Route path='/friend' element={<Friend />}></Route>
          <Route path='/shop' element={<Shop />}></Route>
          <Route path='/musicPerson' element={<MusicPerson />}></Route>
          <Route path='/search' element={<Search setAudioUrl={setAudioUrl} setAudioNum={setAudioNum} searchData={audioList} searchVal={searchVal} />}></Route>
        </Routes>
      </div>
      <div className='audioPlayer'>
        <div className='pic'>
          <div>{audioList.length > 0 ? <img className='playerImg' src={audioList[audioNum].al.picUrl} /> : ''}</div>
          <div className='playerName'>{audioList.length > 0 ? audioList[audioNum].name : ''}</div>
        </div>
        <div className='controls'>

          <audio id='audioControls' onEnded={() => { nextClick() }} style={{ width: "60vw" }} controls src={audioUrl}> </audio>

        </div>
        <div className='nextPrev'>
          <div className='playerPrev'>
            <StepBackwardOutlined className={`${audioList.length > 0 ? '' : 'disabled'} prevBtn`} onClick={() => { prevClick() }} />
          </div>
          <div className='playernext'>
            <StepForwardOutlined className={`${audioList.length > 0 ? '' : 'disabled'} nextBtn`} onClick={() => { nextClick() }} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
