import { get } from "../utils/request"

// 登录
export function login(params) {
    return get('/login', params)
}

// 轮播图区域
export function getBanner(params) {
    return get('/banner', params)
}

// 获取轮播图下面的热门歌单
export function getHot(params){
    return get('/top/playlist', params)
}

// 获取热门歌曲下面的新碟上架
export function getAlbum(params){
    return get('/top/album', params)
}

// 根据歌单获取歌曲列表
export function getHotIds(params){
    return get('/playlist/track/all', params)
}

// 根据id获取音乐的url
export function getMusicUrl(params){
    return get('/song/url', params)
}

// 根据搜索的关键字获取搜索的数据
export function getSearchMusic(params){
    return get('/cloudsearch', params)
}