import "./index.css"
import { LeftOutlined, RightOutlined, PlayCircleOutlined } from '@ant-design/icons';
import { createFromIconfontCN } from '@ant-design/icons';
import { Space } from 'antd';
import { getMusicUrl, getHotIds, getSearchMusic } from '../../api/index'

const IconFont = createFromIconfontCN({
    scriptUrl: '//at.alicdn.com/t/c/font_3843235_s3ss4m5g6jg.js',
});
function Index(props){
    console.log('你好这里是搜索页面:',props)
    const {searchData,setAudioNum,setAudioUrl} = props
    var audioPlayer = document.getElementById('audioControls')
    console.log(audioPlayer)


    const start = (index,id)=>{
        console.log('点击播放的音乐是:',index)
        setAudioNum(index)
        getMusicUrl({ id }).then(res2 => {
            console.log('获取音乐的地址是', res2)
            setAudioUrl(res2.data.data[0].url)
            // 自动播放音乐
            var audioPlayer = document.getElementById('audioControls')
            console.log(audioPlayer)
            setTimeout(() => {
              audioPlayer.play()
            }, 100)
          })
    }
    return (
        <div className="searchContainer">
            <div className="hotContent">
                    {
                        searchData.map((item,index) => <div key={item.id} className="hotItem">
                            <div className="imgBox">
                                <img src={item.al.picUrl}></img>
                                <img className="imgBg" src={item.al.picUrl}></img>
                                <div className="numStart">
                                    <div className="num"><Space><IconFont style={{ fontSize: '30px', color: '#eee' }} type="icon-music" /></Space>{item.v}万</div>
                                    <div className="start"><PlayCircleOutlined onClick={()=>{start(index,item.id)}}  className="player" style={{ fontSize: '20px', color: '#eee' }} /></div>
                                </div>
                            </div>
                            <div className="musicName">
                                {item.name}
                            </div>

                        </div>)
                    }
                </div>
        </div>
    )
}

export default Index;