import { useState, useEffect, useRef } from "react"
import { getBanner, getHot, getAlbum } from "../../api/index"
import { Carousel } from 'antd';
import { LeftOutlined, RightOutlined, PlayCircleOutlined } from '@ant-design/icons';
import { createFromIconfontCN } from '@ant-design/icons';
import { Space } from 'antd';
import React from 'react';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import './index.css'
const IconFont = createFromIconfontCN({
    scriptUrl: '//at.alicdn.com/t/c/font_3843235_s3ss4m5g6jg.js',
});
const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
function Index(props) {

    const { getCount } = props;
    const addCount = (val) => {
        console.log(props)
        // setCount(val);
        getCount(val);
      };
    // 轮播
    var [bannerList, setBanner] = useState([])
    // 热门歌曲
    var [hotMusic, setHotMusic] = useState([])
    // 新碟上架
    var [newAlbum, setNewAlbum] = useState([])
    const [loading, setLoading] = useState(true);
    const carousel = useRef(null)
   
    useEffect(() => {
        const fetchData = async () => {
            // 获取轮播
            const result = await getBanner({ type: 0 })
            // 获取热门歌曲
            const result2 = await getHot({ limit: 8, order: 'hot' })
            // 获取新碟上架
            const result3 = await getAlbum({ limit: 4, area: "ALL", type: 'new', offset: 0, year: "2022", month: "12" })
            var res3Data = []
            result3.data.monthData.forEach((item, index) => {
                if (index < 4) {
                    res3Data.push(item)
                }
            })
            setBanner(result.data.banners)
            setHotMusic(result2.data.playlists)
            setNewAlbum(res3Data)
            setLoading(false)
        }
        fetchData()
    }, [])
    return (
        <div>
            <Spin spinning={loading} size="large" indicator={antIcon} tip="数据加载中...">
            <div className="banner">
                <div className="prev" onClick={() => { carousel.current.prev() }}> <LeftOutlined height={50} /></div>
                <Carousel autoplay ref={carousel}>
                    {
                        bannerList.map(item => <div className="bannerItem">
                            <div style={{ position: 'absolute', width: '300%', height: '300%', left: '-5px', top: '-100%', background: "url('" + item.imageUrl + "') center center fixed", filter: 'blur(20px)', zIndex: '-2' }}></div>
                            <img src={item.imageUrl} ></img>
                        </div>)

                    }
                </Carousel>
                <div className="next" onClick={() => { carousel.current.next() }}> <RightOutlined height={50} /></div>

            </div>
            <div className="hot">
                <div className="hotHeader">
                    <div className="left">
                        <div className="title">热门推荐</div>
                        <div className="typeLsit"></div>
                    </div>
                    <div className="right">更多-&gt;</div>
                </div>
                <div className="hotContent">
                    {
                        hotMusic.map(item => <div key={item.id} className="hotItem">
                            <div className="imgBox">
                                <img src={item.coverImgUrl}></img>
                                <img className="imgBg" src={item.coverImgUrl}></img>
                                <div className="numStart">
                                    <div className="num"><Space><IconFont style={{ fontSize: '30px', color: '#eee' }} type="icon-music" /></Space>{item.trackCount}万</div>
                                    <div className="start"><PlayCircleOutlined onClick={()=>{addCount(item.id)}} className="player" style={{ fontSize: '20px', color: '#eee' }} /></div>
                                </div>
                            </div>
                            <div className="musicName">
                                {item.name}
                            </div>

                        </div>)
                    }
                </div>
            </div>
            <div className="hot">
                <div className="hotHeader">
                    <div className="left">
                        <div className="title">新碟上架</div>
                        <div className="typeLsit"></div>
                    </div>
                    <div className="right">更多-&gt;</div>
                </div>
                <div className="hotContent">
                    {
                        newAlbum.map(item => <div key={item.id} className="hotItem">
                            <div className="imgBox imgBox2">
                                <img src={item.picUrl}></img>
                                <img className="imgBg" src={item.picUrl}></img>
                                <PlayCircleOutlined className="player2" style={{ fontSize: '20px', color: 'grey' }} />
                            </div>
                            <div className="musicName">
                                {item.name}
                            </div>

                        </div>)
                    }
                </div>
            </div>
            </Spin>
        </div >
    )
}
export default Index